<template>
  <div>
    <div>
      <div style="text-align:center;"><h3></h3></div>
      <el-form ref="form" :model="tableData" label-width="100px">
        <el-form-item label="">
          <el-select v-model="tableData.buildingId" clearable placeholder="">
            <el-option
                v-for="item in tableData.options"
                :key="item.buildingId"
                :label="item.name"
                :value="item.buildingId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="tableData.houseNumber" style="width: 21%;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addRoom"></el-button>
          <el-button @click="$router.go(-1)"></el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script>
import { getBuildingTip } from '@/api/building'
import { addRoom } from '@/api/room'
export default {
  name: "InsertHouse",
  data() {
    return {
      tableData: {
        buildingId:'',
        houseNumber:"",
        options: [],
      },
      rules:{
        username:[
          {required: true, message: '', trigger: 'blur'},
          {min:4,max:20,message: '4',trigger: 'blur'}
        ],
        password:[
          {required: true, message: '', trigger: 'blur'},
          {min:6,message: '6',trigger: 'blur'}
        ]
      }

    }
  },
  methods:{
    //
    validateRoomInfo(){
      if (this.tableData.buildingId <= 0 ||
        this.tableData.houseNumber === "" ||
        !/^[0-9]+$/.test(this.tableData.houseNumber)||
        !/^[0-9]+$/.test(this.tableData.buildingId)) {
        this.$message.error("")
        return false
      }
      return true
    },
    //
    addRoom(){
      //
      if(!this.validateRoomInfo()) return
      //
      addRoom(this.tableData.buildingId, this.tableData.houseNumber).then(()=>{
         this.$message.success("")
         this.tableData.houseNumber = "";
      })

      //

      // ajax

    },
    // 
    getBuildingTip(){
      getBuildingTip().then(res=>{
        res.data.forEach(item => {
          let temp = {
            buildingId: item.buildingId,
            name: item.name
          }
          this.tableData.options.push(temp)

        })
      })
    }
  },
  created() {
    this.getBuildingTip()
  }
}
</script>

