<template>
  <div>
    <header style="text-align:center;margin-bottom: 10px;">
      <h3></h3>
    </header>
    <!--  -->
    <div>
      <el-form class="search-form" :model="searchForm" :inline="true" :label-position="'left'">
        <el-form-item label="" prop="buildingName">
          <el-autocomplete v-model="searchForm.buildingName" :fetch-suggestions="querySearchAsync" placeholder=""
            clearable>
          </el-autocomplete>

          <!-- <el-input clearable class="input" placeholder="" v-model="searchForm.buildingName"></el-input> -->
        </el-form-item>
        <el-form-item label="" prop="houseNumber">
          <el-input clearable class="input" placeholder="" v-model="searchForm.houseNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rentState">
          <el-select clearable class="rentStatueSelect" v-model="searchForm.rentState" placeholder="">
            <el-option label="" value="true"></el-option>
            <el-option label="" value="false"></el-option>
          </el-select>
        </el-form-item>
        <span class="search-button1">
          <el-form-item>
            <el-button type="primary" @click="queryRoom"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button @click="handleReset"></el-button>
          </el-form-item>
        </span>
      </el-form>
    </div>
    <!--  -->
    <div style="height: 15px; "></div>
    <!--  -->
    <!--   -->
    <div>
      <el-button @click="goInsert" class="addButton" type="success" icon="el-icon-plus" size="medium"></el-button>
      <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
        background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
      </el-pagination>
    </div>

    <!--  -->
    <!--  -->
    <el-table :data="tableData" v-loading="showLoading" style="width: 100%;font-size: 16px;"
      :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item class="subfield-title">
              <span></span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.houseInfo.buildingId }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.houseInfo.buildingName }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.houseInfo.address }}</span>
            </el-form-item>
            <el-form-item class="subfield-title">
              <span></span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.houseInfo.roomId }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.houseInfo.houseNumber }}</span>
            </el-form-item>
            <el-form-item class="subfield-title">
              <span></span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.houseState.rentState ? '' : '' }}</span>
            </el-form-item>
            <div v-show="props.row.houseState.rentState">
              <el-form-item label="-">
                <span>{{ props.row.renterInfo.username }}</span>
              </el-form-item>
              <el-form-item label="-">
                <span>{{ props.row.renterInfo.name }}</span>
              </el-form-item>
              <el-form-item label="-">
                <span>{{ props.row.renterInfo.phoneNumber }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.houseState.createdTime }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.houseState.stopTime == null ? "" : props.row.houseState.stopTime }}</span>
              </el-form-item>
            </div>

          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" prop="houseInfo.buildingName" align="center">
      </el-table-column>
      <el-table-column label="" prop="houseInfo.houseNumber" align="center" sortable>
      </el-table-column>
      <el-table-column label="" prop="houseState.rentState" align="center">
        <template slot-scope="props">
          <span>
            {{ props.row.houseState.rentState ? '' : '' }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="" align="center">
        <template slot-scope="props">
          <div>
            <el-button type="primary" round style="width: 45%;" @click="toUpdate(props.row)">
            </el-button>
            <el-button type="danger" round style="width: 45%;" @click="deleteHouse(props.row.houseInfo.roomId)">
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>


<script>
import { queryRoom, deleteRoom } from '@/api/room'
import { getBuildingTip } from '@/api/building'

export default {
  data() {
    return {
      searchForm: {
        buildingName: '',
        houseNumber: '',
        rentState: ''

      },
      // 
      buildingTipList: [],
      page: 1,
      pageSize: 10,
      pageTotalSize: 20,
      tableData: [{
        //
        renterInfo: {
          userId: '',
          name: '',
          username: '',
          phoneNumber: ''
        },
        //
        houseInfo: {
          buildingId: '',
          buildingName: '',
          address: '',
          roomId: '',
          houseNumber: '',
        },
        //
        houseState: {
          rentingId:'',
          rentState: '',
          createdTime: '',
          stopTime: ''
        }
      }],
      showLoading: true,
      choose: "",
      search: ""
    }
  },
  computed: {
    filterData() {

      if (this.choose && this.search === "") return this.tableData.filter((item) => {
        return item.buildingInfo.buildingId === this.choose;
      })
      if (this.choose && this.search) return this.tableData.filter((item) => {
        return item.buildingInfo.buildingId === this.choose && item.houseInfo.houseNumber.indexOf(this.search) !== -1
      })
      if (!this.choose && this.search) return this.tableData.filter((item) => {
        return item.houseInfo.houseNumber.indexOf(this.search) !== -1
      })
      return this.tableData

    },
    searchBuilding() {
      let option = []
      this.tableData.forEach(item => {
        let buildingId = item.buildingInfo.buildingId
        let fall = true;
        option.forEach(item2 => {
          if (buildingId === item2.buildingId) {
            fall = false;
          }
        })
        if (fall) {
          let temp = {
            buildingId: buildingId,
            name: item.buildingInfo.name
          }
          option.push(temp)
        }
      })

      return option;
    }
  },
  methods: {
    //
    handleReset() {
      this.searchForm = {
        buildingName: '',
        houseNumber: '',
        rentState: ''
      }
    },
    //
    queryRoom() {
      this.showLoading = true;

      queryRoom(this.page,
        this.pageSize,
        this.searchForm.buildingName,
        this.searchForm.houseNumber,
        this.searchForm.rentState).then(res => {
          this.handleRoomInfo(res.data)
        })

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.showLoading = false;
      }, 600 * Math.random());
    },
    //
    handleRoomInfo(data) {
      this.tableData = []
      //
      this.pageTotalSize = data.count
      data.list.forEach(item => {
        // console.log(item)
        let tableDataItem = {
          renterInfo: '',
          houseInfo: '',
          houseState: '',
        }
        //
        tableDataItem.houseInfo = {
          buildingId: item.buildingId,
          buildingName: item.buildingName,
          address: item.address,
          roomId: item.roomId,
          houseNumber: item.houseNumber,
        }
        //
        if (item.rentState) {
          //
          tableDataItem.houseState = {
            rentingId:item.rentingId,
            rentState: item.rentState,
            createdTime: item.createdTime.slice(0, 19).replace('T', " "),
            stopTime: item.stopTime == null ? null : item.stopTime.slice(0, 19).replace('T', " ")
          }
          //
          tableDataItem.renterInfo = {
            userId: item.userId,
            name: item.name,
            username: item.username,
            phoneNumber: item.phoneNumber
          }
        }
        this.tableData.push(tableDataItem)
      })
    },
    //
    deleteHouse(roomId) {
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].houseInfo.roomId === roomId) {
          // this.tableData.splice(i,1);
          break;
        }
      }

      this.$confirm(`${this.tableData[i].houseInfo.buildingName}${this.tableData[i].houseInfo.houseNumber}, `,
        '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      }).then(() => {
        deleteRoom(roomId).then(()=>{
          this.$message({
            type: 'success',
            message: '!'
          });
          this.tableData.splice(i, 1)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });


    },
    //
    toUpdate(row) {
      this.$router.push({
        path: '/manage/updateHouseInfo',
        query: {
          tableData: JSON.stringify(row)
        }
      })
    },
    //
    goInsert() {
      this.$router.push({ path: "/manage/insertHouse" });
    },
    //
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.queryRoom();
    },
    //
    handleCurrentChange(page) {
      this.page = page;
      this.queryRoom();
    },
    //
    querySearchAsync(queryString, callback) {

      var options = this.buildingTipList;

      //
      var results = queryString ? options.filter(item => {
        return (item.value.indexOf(queryString.toLowerCase()) != -1)
      }) : options;
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        callback(results);
      }, 500 * Math.random());
    },
    //
    getBuildingTip() {
      getBuildingTip().then(res => {
        res.data.forEach(item => {
          let temp = {
            buildingId: item.buildingId,
            value: item.name
          }
          this.buildingTipList.push(temp)
        })
      })
    },
    // 
    loadData() {
      this.queryRoom()
      this.getBuildingTip()
    }


  },
  //
  beforeRouteEnter(to, from, next) {
    if('/manage/updateHouseInfo'== from.path){
      next((vm) => {
        vm.queryRoom()
      })      
    }
    next()
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style scoped>
/*  */
.search-button1 {
  display: block;
  float: right;
}

/*  */
.addButton {
  margin-bottom: 20px;
}

/*  */
.renterPagination {
  float: right;
  text-align: right;
}

/*  */
.subfield-title {
  color: black;
  font-weight: 600;
  margin-left: 10%
}


</style>