<template>
  <div>
    <el-form class="search-form" :model="searchForm" :inline="true" :label-position="'left'">
      <el-form-item label="" prop="username">
        <el-input class="input" placeholder="" v-model="searchForm.username"></el-input>
      </el-form-item>
      <el-form-item label=" " prop="name">
        <el-input class="input" placeholder="" v-model="searchForm.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input class="input" placeholder="" v-model="searchForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="rentState">
        <el-select class="rentStatueSelect" v-model="searchForm.rentState" placeholder="">
          <el-option label="" :value="true"></el-option>
          <el-option label="" :value="false"></el-option>
        </el-select>
      </el-form-item>
      <span class="search-button">
        <el-form-item>
          <el-button type="primary" @click="queryRenter"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="handleReset"></el-button>
        </el-form-item>
      </span>
    </el-form>
    <!--  -->
    <div style="height: 15px; "></div>

    <!--   -->
    <div>
      <el-button @click="goInsert" class="addButton" type="success" icon="el-icon-plus" size="medium"></el-button>
      <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
        background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
      </el-pagination>
    </div>
    <!--  -->
    <el-table :data="filterData" v-loading="showLoading" element-loading-text="..."
      :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item style="color: black;margin-left: 10%">
              <h3></h3>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.userId }}</span>
            </el-form-item>
            <el-form-item label=" ID">
              <span>{{ props.row.roleId }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.roleDescription }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.username }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.sex === "2" ? "" : props.row.sex === "1" ? "" : "" }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.phoneNumber }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.identity }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.locked ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.enable ? '' : '' }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.rentState ? '' : '' }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" prop="username" align="center">
      </el-table-column>
      <el-table-column label="" prop="name" align="center">
      </el-table-column>
      <el-table-column label="" prop="phoneNumber" align="center">
      </el-table-column>
      <el-table-column label="" prop="houseState.rentState" align="center">
        <template slot-scope="props">
          <span>
            {{ props.row.rentState ? '' : '' }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="" align="center">
        <template slot-scope="props">
          <div>
            <el-button type="primary" round style="width: 45%;" @click="$router.push({
              path: '/manage/updateRenter',
              query: {
                isHasData: true,
                tableData: JSON.stringify(filterData[props.$index])
              }
            })">
            </el-button>
            <el-button type="danger" round style="width: 45%;" @click="triggerDeleteRenter(props, props.row.userId)">
            </el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div>
      <el-dialog class="deletef" title="" :visible.sync="confirmDialogVisible" :close-on-click-modal="false"
        :close-on-press-escape="false" width="30%">
        <p class="contentp">{{ message }}</p>
        <div class="buttonf">

          <div v-if="countdown > 0" class="button">
            <el-button type="danger" :disabled="true">{{ countdown }}S</el-button>
            <el-button @click="confirmDialogVisible = false"></el-button>
          </div>

          <div v-else class="button">
            <el-button type="danger" :loading="deleting" @click="deleteRenter"></el-button>
            <el-button @click="confirmDialogVisible = false"></el-button>
          </div>

        </div>
        <div class="empty"></div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { queryRenter, deleteRenter, getTotalRenter } from '@/api/renter'


export default {
  name: "QueryRenter",
  data() {
    return {
      search: "",
      tableData: [],
      showLoading: true,
      page: 1,
      pageSize: 10,
      pageTotalSize: 20,
      searchForm: {
        username: "",
        name: "",
        phoneNumber: "",
        rentState: ""
      },
      //
      confirmDialogVisible: false,
      countdown: 0,
      deleting: false,
      countdownIntervalId: null,
      message:'',
      readyDeleteProps:'',
      readyDeleteUserId: '',
    };
  },
  computed: {
    filterData() {
      return this.tableData.filter((item) => {
        return item.username.indexOf(this.search) !== -1;
      });
    }
  },
  methods: {
    //
    handleReset() {
      this.searchForm = {
        username: "",
        name: "",
        phoneNumber: "",
        rentState: ""
      };
    },
    //
    queryRenter() {
      this.tableData = [];
      this.showLoading = true;
      let rentState = "";
      switch (this.searchForm.rentState) {
        case "1":
          rentState = true;
          break;
        case "0":
          rentState = false;
          break;
        default: rentState = "";
      }
      queryRenter(this.page, this.pageSize, this.searchForm.username, this.searchForm.name, this.searchForm.phoneNumber, rentState).then(response => {
        response.data.forEach(data => {
          let temp = {
            userId: data.uUserVol.userId,
            roleId: data.uUserVol.roleId,
            roleDescription: data.uRole.description,
            username: data.uUserVol.username,
            password: "",
            sex: data.uUserVol.sex,
            name: data.uUserVol.name,
            phoneNumber: data.uUserVol.phoneNumber,
            identity: data.uUserVol.identity,
            locked: data.uUserVol.locked === "1",
            enable: data.uUserVol.enable === "1",
            createdTime: "2021-11-20 13:19:00",
            updateTime: data.uUserVol.updateTime,
            rentState: data.rentState
          };
          this.tableData.push(temp);
        });
      });
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.showLoading = false;
      }, 600 * Math.random());
    },
    //
    getTotalRenter() {
      getTotalRenter().then(response => {
        this.pageTotalSize = response.data;
      });
    },
    //
    handleDelete(index) {
      let result = (this.page === Math.ceil(this.pageTotalSize / this.pageSize)) && index === 0 && this.tableData.length === 0;
      if (result) {
        if (this.page > 1)
          this.page--;
        this.loadData();
      }
    },
    //
    triggerDeleteRenter(props, userId) {
      //
      this.showConfirmDialog()
      //
      this.readyDeleteProps=props
      this.readyDeleteUserId=userId
      //
      let i;
      for (i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].userId === userId) {
          break;
        }
      }
      this.message = `${this.tableData[i].username}${this.tableData[i].name}, `
      
    },
    //
    deleteRenter(){
      deleteRenter(this.readyDeleteUserId).then(response => {
        if (response.data) {
          this.$message({
            type: "success",
            message: "!"
          });
          //
          let i;
          for (i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].userId === this.readyDeleteUserId) {
              break;
            }
          }
          this.tableData.splice(i, 1);
          this.handleDelete(this.readyDeleteProps.$index);

          this.getTotalRenter();
          this.confirmDialogVisible = false
          clearInterval(this.countdownIntervalId)
        }
        else {
          this.$message({
            type: "error",
            message: ""
          });
        }
      });

    },
    //
    goInsert() {
      this.$router.push({ path: "/manage/insertRenter" });
    },
    //
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.queryRenter();
    },
    //
    handleCurrentChange(page) {
      this.page = page;
      this.queryRenter();
    },
    //
    loadData() {
      this.queryRenter();
      this.getTotalRenter();
    },
    //
    showConfirmDialog() {
      this.confirmDialogVisible = true;
      this.countdown = 2;
      this.countdownIntervalId = setInterval(() => {
        this.countdown--;
        if (this.countdown === 0) {
          clearInterval(this.countdownIntervalId);
        }
      }, 1000);
    },
    //
    deleteItem() {
      this.deleting = true;
      // TODO: 
      setTimeout(() => {
        this.deleting = false;
        this.confirmDialogVisible = false;
      }, 1000);
    },
  },
  mounted() {
    this.loadData()
  },
  components: {}
}
</script>

<style scoped>
/*  */
.renterPagination {
  float: right;
  text-align: right;
}

/*  */
.input {
  float: left;
  width: 170px;
}

/*  */
.addButton {
  margin-bottom: 20px;
}

/*  */
.rentStatueSelect {
  width: 110px;
}

/*  */
.search-button {
  display: block;
  float: right;
}
/*  */
.contentp{
    padding: 0 15px 15px 15px;
}
.buttonf {
    height: 38px;
}

.button {
    float: right;
}
</style>