<template>
    <div>
        <div style="text-align:center;margin-bottom: 10px;">
            <h3></h3>
        </div>
        <!--  -->
        <div>
            <el-form class="search-form" :model="searchForm" :inline="true" :label-position="'left'">
                <el-form-item label="" prop="buildingName">
                    <el-autocomplete v-model="searchForm.buildingName" :fetch-suggestions="querySearchAsync"
                        placeholder="" clearable>
                    </el-autocomplete>
                </el-form-item>
                <el-form-item label="" prop="houseNumber">
                    <el-input clearable placeholder="" v-model="searchForm.houseNumber"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="searchForm.readingDate" type="month"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>

                <!-- <el-form-item label="" prop="rentState">
                    <el-select clearable class="rentStatueSelect input" v-model="searchForm.rentState" placeholder="">
                        <el-option label="" value="true"></el-option>
                        <el-option label="" value="false"></el-option>
                    </el-select>
                </el-form-item> -->

                <span class="search-button1">
                    <el-form-item>
                        <el-button type="primary" @click="queryMeterReading"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="handleReset"></el-button>
                    </el-form-item>
                </span>
            </el-form>
        </div>
        <!--  -->
        <div style="height: 15px; "></div>
        <div class="add-pagination clearfix">
            <div>
                <el-button class="add" type="success" @click="toAddMeterReading"></el-button>
                <el-button class="add" slot="trigger" @click="batchAddDialogVisible = true"
                    type="primary"></el-button>
            </div>
            <el-pagination class="renterPagination" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                background layout="total, sizes, prev, pager, next, jumper" :total="pageTotalSize">
            </el-pagination>
        </div>
        <el-table :data="tableData" style="width: 100%;font-size: 16px;"
            :header-cell-style="{ background: '#f0f0f0', color: '#333' }">
            <el-table-column prop="buildingName" label="" align="center"></el-table-column>
            <el-table-column prop="houseNumber" label="" align="center"></el-table-column>
            <el-table-column prop="waterReading" label="/ " align="center"></el-table-column>
            <el-table-column prop="electricityReading" label="/ " align="center"></el-table-column>
            <el-table-column prop="readingDateVo" label="" align="center"></el-table-column>
            <el-table-column prop="address" label="" align="center">
                <template slot-scope="props">
                    <div>
                        <el-button type="primary" round style="width: 38%;margin-right: 5px;"
                            @click="editMeterReading(props.row)">
                        </el-button>
                        <el-popconfirm title="" @confirm="deleteMeterReading(props.row)">
                            <el-button slot="reference" type="danger" round style="width: 38%;">
                            </el-button>
                        </el-popconfirm>

                    </div>
                </template>
            </el-table-column>

            <!-- <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="editMeterReading(scope.row)"></el-button>
                    <el-button type="text" @click="deleteMeterReading(scope.row)"></el-button>
                </template>
            </el-table-column> -->
        </el-table>
        <!--  -->
        <div>
            <!--  -->
            <el-dialog :visible.sync="dialogVisible" title="">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="">
                        <el-select v-model="form.buildingId" @change="listRoomWhenChooseBuilding" placeholder="">
                            <el-option v-for="building in buildings" :key="building.buildingId"
                                :label="building.buildingName" :value="building.buildingId"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select :disabled="roomSelectAble" v-model="form.roomId" placeholder="" clearable>
                            <el-option v-for="room in rooms" :key="room.roomId" :label="room.houseNumber"
                                :value="room.roomId"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="form.readingDate" type="date"
                            placeholder=""></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.waterReading"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.electricityReading"></el-input>
                    </el-form-item>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false"> </el-button>
                    <el-button @click="addMeterReading" type="primary"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog :visible.sync="dialogVisibleUpdate" title="">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="">
                        <span> {{ form.buildingName }} </span>

                    </el-form-item>
                    <el-form-item label="">
                        <span>{{ form.houseNumber }}</span>

                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="form.readingDate" type="date"
                            placeholder=""></el-date-picker>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.waterReading"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.electricityReading"></el-input>
                    </el-form-item>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisibleUpdate = false"> </el-button>
                    <el-button @click="updateMeterReading" type="primary"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog :visible.sync="batchAddDialogVisible" title="">
                <h4 class="batchTitle"></h4>
                <el-form ref="batchForm" :model="form" label-width="100px" style="margin-bottom: 20px;">
                    <el-form-item label="" prop="buildingName">
                        <el-autocomplete v-model="batchForm.buildingName" :fetch-suggestions="querySearchAsync"
                            placeholder="" @select="handleBatchSelect" clearable>
                        </el-autocomplete>
                    </el-form-item>
                    <el-form-item label="">
                        <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="batchForm.readingDate" type="date"
                            placeholder=""></el-date-picker>
                    </el-form-item>
                </el-form>
                <div style=" margin-top: 30px;margin-left: 25px;" class="clearfix">
                    <a :href="fileUrl" download></a>
                    <el-button class="add" type="success" @click="getExcelTemplate"> Excel </el-button>
                    <el-upload class="add upload-demo" action="" style="margin-left: 30px;" :on-change="handleUpload"
                        :before-upload="beforeUpload"
                        :accept="'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'">
                        <el-tooltip slot="trigger" class="item" effect="dark" content="" placement="top">
                            <el-button type="primary">Excel</el-button>
                        </el-tooltip>
                        <div slot="tip" class="el-upload__tip"> .xlsx Excel</div>
                    </el-upload>
                    <el-button type="warning" @click="handleList" v-show="showHandleExcel"></el-button>
                </div>
                <!--  -->
                <div slot="footer" class="dialog-footer">
                    <el-button @click="batchAddDialogVisible = false"> </el-button>
                    <!-- <el-button type="primary"> </el-button> -->
                </div>
            </el-dialog>
        </div>

    </div>
</template>
<script>
import * as XLSX from 'xlsx'
import { getBuildingTip } from '@/api/building'
import { deleteMeterReading, queryMeterReading, addMeterReading, updateMeterReading, handleList } from '@/api/meterReading'
import { listRoomByBuildingId } from '@/api/room'
export default {
    data() {
        return {
            searchForm: {
                readingDate: '',
                buildingName: '',
                houseNumber: '',
                rentState: ''

            },
            page: 1,
            pageSize: 10,
            pageTotalSize: 20,
            // 
            buildingTipList: [],
            tableData: {
                Id: '',
                buildingId: '',
                buildingName: '',
                roomId: '',
                houseNumber: '',
                waterReading: '',
                electricityReading: ''
            },
            dialogVisible: false,
            //
            form: {
                buildingId: '',
                roomId: '',
                houseNumber: '',
                readingDate: '',
                waterReading: null,
                electricityReading: null
            },
            //
            buildings: [],
            //
            roomSelectAble: true,
            rooms: [],
            importDialogVisible: false,
            file: null,
            // 
            batchForm: {
                buildingId: '',
                buildingName: '',
                readingDate: '',
            },
            //
            showHandleExcel: false,
            readingDate: '',
            batchAddDialogVisible: false,
            dialogVisibleUpdate: false,
            waterElectricityList: [], // 
            excelHeader: [],
            //
            updateMeterReadingId: '',
            fileUrl: ''

        }
    },
    created() {
        this.queryMeterReading()
        this.getBuildingTip()
    },
    methods: {
        // 
        handleUpload(file) {
            const reader = new FileReader()
            reader.onload = (e) => {
                const data = new Uint8Array(e.target.result)
                const workbook = XLSX.read(data, { type: 'array' })
                const worksheet = workbook.Sheets[workbook.SheetNames[0]]
                // waterElectricityData  Excel 
                const waterElectricityData = XLSX.utils.sheet_to_json(worksheet)
                // 
                const headerData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
                const header = headerData[0];
                this.excelHeader = header
                // 
                this.waterElectricityList = waterElectricityData;

                let newObj = []
                waterElectricityData.forEach((item) => {
                    let temp = {
                        buildingId: item[''],
                        buildingName: item[''],
                        roomId: item[''],
                        houseNumber: item[''],
                        waterReading: item[''],
                        electricityReading: item[''],
                    }
                    if (temp.waterReading != "" || temp.electricityReading != "") {
                        newObj.push(temp)
                    }

                })
                this.waterElectricityList = newObj

            }
            reader.readAsArrayBuffer(file.raw)
        },
        beforeUpload(file) {
            const fileType = file.name.split('.').pop()
            if (fileType !== 'xlsx') {
                this.$message.error(' .xlsx Excel')
                return false
            }
            // 
            const str = file.name
            const regex = /^([^(]+)[(](\d{4}-\d{1,2}-\d{1,2})[)]/;
            const result = str.match(regex);

            if (result) {
                const name = result[1]; // ()
                const time = result[2]; // ()
                this.batchForm.readingDate = time + " 00:00:00"
                this.batchForm.buildingName = name
                this.buildings.forEach(item => {
                    if (item.buildingName == name) {
                        this.batchForm.buildingId = item.buildingId
                    }
                })
                this.showHandleExcel = true
            } else {
                this.$message.error('(--)')
                this.showHandleExcel = false
                return false
            }
        },
        //
        handleList() {
            if (this.waterElectricityList != null && this.waterElectricityList.length != 0) {
                // console.log(this.waterElectricityList)
                handleList(this.waterElectricityList, this.batchForm.readingDate).then(() => {
                    this.$message.success("")
                    //
                    this.showHandleExcel = false
                    this.batchForm.readingDate = ''
                    this.batchForm.buildingName = ''
                    this.batchForm.buildingId = ''
                })
            }

        },
        //
        handleBatchSelect(item) {
            this.batchForm.buildingId = item.buildingId
        },
        // 
        queryMeterReading() {

            this.tableData = []
            let that = this.searchForm
            queryMeterReading(this.page, this.pageSize,
                that.buildingName, that.houseNumber,
                that.readingDate,
                // that.rentState
            ).then(res => {
                let data = res.data
                this.pageTotalSize = data.count
                data.list.forEach(item => {
                    let temp = {
                        id: item.id,
                        buildingName: item.buildingName,
                        roomId: item.roomId,
                        houseNumber: item.houseNumber,
                        readingDate: item.readingDate,
                        readingDateVo: item.readingDate.substr(0, 10),
                        waterReading: item.waterReading,
                        electricityReading: item.electricityReading
                    }
                    this.tableData.push(temp)

                })

            })
        },
        //
        handleReset() {
            this.searchForm = {
                buildingName: '',
                houseNumber: '',
                readingDate: '',
                // rentState: ''
            }
        },
        //
        handleSizeChange(pageSize) {
            this.pageSize = pageSize;
            this.queryMeterReading()
        },
        //
        handleCurrentChange(page) {
            this.page = page;
            this.queryMeterReading()
        },
        //
        addMeterReading() {
            addMeterReading(this.form.buildingId, this.form.roomId, this.form.readingDate, this.form.waterReading, this.form.electricityReading).then(() => {
                this.$message.success("")
                this.dialogVisible = false
                this.queryMeterReading()
            })
        },
        fetchRooms() {
            // API
            // ...
            this.rooms = [{ id: 1, roomId: 'A101' }, { id: 2, roomId: 'A102' }, { id: 3, roomId: 'A103' }]
        },
        toAddMeterReading() {
            this.form = {
                buildingId: '',
                roomId: '',
                houseNumber: '',
                readingDate: '',
                waterReading: null,
                electricityReading: null
            }
            this.roomSelectAble = true
            this.dialogVisible = true

        },
        //
        listRoomWhenChooseBuilding() {
            this.form.houseNumber = ''
            this.roomSelectAble = false
            listRoomByBuildingId(this.form.buildingId).then(res => {
                this.rooms = []
                res.data.forEach(item => {
                    let temp = {
                        roomId: item.roomId,
                        houseNumber: item.houseNumber
                    }
                    this.rooms.push(temp)
                })
            })
        },
        editMeterReading(row) {
            this.updateMeterReadingId = row.id
            this.form = {
                buildingId: '',
                buildingName: row.buildingName,
                roomId: row.roomId,
                houseNumber: row.houseNumber,
                readingDate: row.readingDate,
                waterReading: row.waterReading,
                electricityReading: row.electricityReading
            }
            this.dialogVisibleUpdate = true
        },
        updateMeterReading() {
            updateMeterReading(
                this.updateMeterReadingId,
                this.form.readingDate,
                this.form.waterReading,
                this.form.electricityReading
            ).then(() => {
                this.$message.success("")
                this.dialogVisibleUpdate = false
                this.queryMeterReading()
            })
        },
        deleteMeterReading(row) {
            deleteMeterReading(row.id).then(() => {
                this.$message.success("")
                this.queryMeterReading()
            })
        },
        //Excel
        getExcelTemplate() {
            if (this.batchForm.buildingId == "" || this.batchForm.readingDate == "") {
                this.$message.error("")
                return false
            }
            listRoomByBuildingId(this.batchForm.buildingId).then(res => {
                if (res.data == null || res.data.length == 0) {
                    this.$message.error("")
                    return false
                } else {
                    this.$message.info(",~")
                    let a = document.createElement("a");
                    a.href = `http://localhost:81/manage/meterReading/downloadExcel?buildingId=${this.batchForm.buildingId}&readingDate=${this.batchForm.readingDate}`;
                    a.click()
                    this.$notify({
                        title: '',
                        message: '4',
                        type: 'warning',
                        position: 'top-left'
                    });
                }
            })


        },
        //
        querySearchAsync(queryString, callback) {

            var options = this.buildingTipList;

            //
            var results = queryString ? options.filter(item => {
                return (item.value.indexOf(queryString.toLowerCase()) != -1)
            }) : options;
            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                callback(results);
            }, 500 * Math.random());
        },
        //
        getBuildingTip() {
            this.buildingTipList = []
            this.buildings = []
            getBuildingTip().then(res => {
                res.data.forEach(item => {
                    let temp = {
                        buildingId: item.buildingId,
                        value: item.name
                    }
                    this.buildingTipList.push(temp)
                    let temp2 = {
                        buildingId: item.buildingId,
                        buildingName: item.name
                    }
                    this.buildings.push(temp2)
                })
            })
        },
    }
}
</script>
<style scoped>
/*  */
.search-button1 {
    display: block;
    float: right;
}

/*  */
.input {
    float: left;
    width: 120px;
}

/*  */
.add-pagination {
    margin-bottom: 10px;
}

/*  */
.renterPagination {
    float: right;
    text-align: right;
}

/*  */
.add {
    float: left;
}

.batchTitle {
    padding-left: 30px;
    padding-bottom: 20px;
}
</style>



